.footer {
  color: white;
  background-color: var(--pink);
  height: 15rem;
  padding-top: 3rem;
  text-align: center;
  width: 100%;
}

.footer p:first-child {
  font-family: Bitter, serif;
  font-size: 2rem;
  margin-bottom: 0;
  text-transform: lowercase;
}

.footer a {
  color: white;
  font-size: 1.4rem;
}

.footer a:hover,
.footer a:focus {
  color: var(--purple);
}
